// hero part start
.section-hero {
  position: relative;
  z-index: 1;
  display: block;
  width: 100%;
  margin-top: -70px;
  overflow: hidden;

  @media (min-width: 1200px) {
    margin-top: -150px;
    aspect-ratio: 21/9;
    max-height: 70vh;
  }

  @media (min-width: 1400px) {
    margin-top: -160px;
  }

  &-content {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    pointer-events: none;

    @media (min-width: 720px) and (max-width: 1199.98px) {
      margin-top: -16px;
    }

    @media (min-width: 1200px) {
      height: 100%;
    }

    & > img {
      position: relative;
      z-index: -1;
      flex: 1;
      width: 100%;
      height: 100%;
      object-fit: cover;

      @media (min-width: 1200px) {
        position: absolute;
        top: 0;
        left: 0;
      }
    }

    & > .container {
      position: relative;
      display: flex;
      flex-direction: column;
      height: 100%;
      align-items: flex-start;
      justify-content: flex-start;
      row-gap: 15px;
      padding: 0;

      @media (min-width: 1200px) {
        padding: 180px 0 20px;
      }

      & > .breadcrumbs {
        display: none;

        @media (min-width: 1200px) {
          display: flex;
        }
      }

      & > .btn {
        position: absolute;
        top: -170px;
        left: 50%;
        white-space: nowrap;
        pointer-events: all;
        transform: translate(-50%, 100%);

        @media (min-width: 720px) {
          display: none;
        }
      }
    }
  }
}

.card-advantage {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: nowrap;
  row-gap: 8px;
  column-gap: 8px;
  width: 100%;
  height: auto;
  min-height: 200px;
  padding: 24px;
  opacity: 1;
  background-color: #eef5fe;
  backdrop-filter: blur(7.95px);
  border: 1px solid #eef5fe;
  pointer-events: all;
  transition: opacity 0.4s ease;

  @media (min-width: 720px) {
    align-items: flex-start;
    justify-content: flex-start;
    row-gap: 10px;
    column-gap: 10px;
    min-height: 265px;
  }

  @media (min-width: 1200px) {
    background-color: rgba(255, 255, 255, 0.7);
    border: 1px solid rgba(255, 255, 255, 0.7);
    max-width: 40%;
    min-height: 235px;
    border-radius: 10px;
  }

  @media (min-width: 1400px) {
    padding: 50px 40px;
  }

  & .card-title {
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    text-align: center;
    color: var(--blue);

    @media (min-width: 720px) {
      font-size: 32px;
      text-align: left;
    }
  }

  & ul {
    font-style: normal;
    font-weight: 300;
    text-align: left;
    flex: 1;

    @media (min-width: 720px) {
      text-align: left;
    }
  }

  & ul {
    list-style: none;
    padding: 0;

    & li {
      position: relative;
      margin: 0 0 25px 0;
      padding-left: 35px;
      font-size: 14px;
      line-height: 1.25;
      @media (min-width: 1200px) {
        font-size: 16px;
      }

      &::before {
        content: url('data:image/svg+xml,<svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M12.5 0C10.8756 0 9.55882 1.31684 9.55882 2.94118V9.55882H2.94118C1.31684 9.55882 0 10.8756 0 12.5C0 14.1244 1.31684 15.4412 2.94118 15.4412H9.55882V22.0588C9.55882 23.6832 10.8756 25 12.5 25C14.1244 25 15.4412 23.6832 15.4412 22.0588V15.4412H22.0588C23.6832 15.4412 25 14.1244 25 12.5C25 10.8756 23.6832 9.55882 22.0588 9.55882H15.4412V2.94118C15.4412 1.31684 14.1244 0 12.5 0Z" fill="%233981F1" /></svg>');
        position: absolute;
        left: 0;
        top: 0;
        display: flex;
        width: 20px;
        height: 20px;
        transform: translate(0, 0);
      }

      &:last-of-type {
        margin: unset;
      }
    }
  }
}

// hero part end

// section connection part start
.section-connection {
  position: relative;
  z-index: 1;
  width: 100%;
  padding: 35px 0;

  @media (min-width: 720px) {
    padding: 40px 0;
  }

  @media (min-width: 1200px) {
    padding: 50px 0;
  }

  & > .container {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 20px;
    column-gap: 20px;

    @media (min-width: 1200px) {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(auto, 100%);
      row-gap: 20px;
      column-gap: 40px;
    }

    & > div {
      &:nth-child(1) {
        & h1 {
          text-align: center;
          @media (min-width: 720px) {
            text-align: left;
          }
        }

        @media (min-width: 992px) {
          & p {
            margin: 0 auto;
            max-width: 80%;
            text-align: left;
          }
        }

        @media (min-width: 1200px) {
          grid-column-start: 1;
          grid-column-end: 3;

          & p {
            margin: 0 auto;
            max-width: 60%;
          }
        }
      }
      &:nth-child(2) {
        & p {
          margin: unset;
        }
      }
    }
  }

  & table {
    width: 100%;
    height: fit-content;
    border-collapse: collapse;

    & tr {
      transition: 0.4s ease;

      &:nth-child(odd) {
        background-color: #eef5fe;
      }

      &:nth-child(even) {
        background-color: #ffffff;
      }

      &:hover {
        background-color: #dadcdf;

        & a {
          text-decoration: none;
        }
      }

      & td {
        color: #585a60;
        padding: 17px 10px 17px 20px;

        @media (min-width: 720px) {
          padding: 17px 10px 17px 50px;
        }

        @media (min-width: 1200px) {
          padding: 17px 10px 17px 84px;
        }
        &:last-of-type {
          padding: 17px 20px 17px 10px;

          @media (min-width: 720px) {
            padding: 17px 50px 17px 10px;
          }

          @media (min-width: 1200px) {
            padding: 17px 84px 17px 10px;
          }
        }

        & a {
          display: block;
          width: 100%;
          height: 100%;
          color: inherit;
        }

        &:last-of-type {
          text-align: right;
          white-space: nowrap;
        }
      }
    }
  }

  & .result-list {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 4px));
    grid-template-rows: repeat(2, 1fr);
    row-gap: 8px;
    column-gap: 8px;
    height: fit-content;
    margin-top: 16px;

    @media (min-width: 720px) {
      row-gap: 20px;
      column-gap: 20px;
      grid-template-columns: repeat(2, calc(50% - 10px));
      margin-top: 32px;
    }

    // @media (min-width: 992px) {
    //   grid-template-columns: repeat(3, 1fr);
    // }
    // @media (min-width: 1200px) {
    //   grid-template-columns: repeat(4, 1fr);
    //   grid-template-rows: auto;
    // }
  }

  & .results-image {
    display: none;
    flex: 1;
    width: 30%;
    max-width: 629px;
    border-radius: 14px;
    overflow: hidden;

    @media (min-width: 1200px) {
      display: flex;
      flex-direction: column;
    }

    & > * {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  & .result-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 20px 5px 30px;
    border: 1px solid rgba(44, 53, 109, 0.3);
    border-radius: 10px;
    background: #ffffff;

    @media (min-width: 720px) {
      padding: 38px 12px 30px;
    }

    @media (min-width: 1200px) {
      padding: 45px 10% 30px;
    }

    @media (min-width: 1400px) {
      padding: 45px 15% 30px;
    }

    & svg,
    & img {
      width: fit-content;
      height: 53px;
      object-fit: contain;
      margin-bottom: 13px;

      @media (min-width: 720px) {
        height: 75px;
      }
    }

    & p {
      margin: unset;
      font-size: 14px;
      line-height: 1.25;
      text-align: center;

      @media (min-width: 720px) {
        font-size: 16px;
        line-height: 20px;
      }
    }
  }
}

// section connection part end

