// section recovery part start
.section-recovery {
  position: relative;
  z-index: 1;
  width: 100%;
  padding: 35px 0 0 0;

  @media (min-width: 720px) {
    padding: 40px 0 0 0;
  }

  @media (min-width: 1200px) {
    padding: 50px 0 0 0;
  }

  & .section-title {
    text-align: left;
