// section doctors part start
.section-doctors {
  position: relative;
  padding: 30px 0;
  background-color: rgba(17, 118, 234, 0.07);
  overflow: hidden;

  @media (min-width: 720px) {
    padding: 40px 0;
  }

  @media (min-width: 1200px) {
    padding: 50px 0;
  }

  & .swiper-doctors {
    position: relative;
    z-index: 1;
    margin-top: 18px;

    @media (min-width: 720px) {
      margin-top: 31px;
    }

    @media (min-width: 1200px) {
      margin-top: 22px;
    }

    & .swiper-container {
      position: relative;
      z-index: 1;

      @media screen and (max-width: 1199.98px) {
        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          z-index: 10;
          border: 0;
          width: 20%;
          height: 100%;
          border-radius: 0;
          background: linear-gradient(
            90deg,
            rgba(17, 118, 234, 0.35) 0%,
            rgba(255, 255, 255, 0.35) 100%
          );
          backface-visibility: hidden;
        }

        &::after {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          z-index: 10;
          border: 0;
          width: 20%;
          height: 100%;
          border-radius: 0;
          background: linear-gradient(
            90deg,
            rgba(255, 255, 255, 0) 0%,
            rgba(17, 118, 234, 0.35) 100%
          );
          backface-visibility: hidden;
        }
      }
    }

    & .swiper-wrapper {
      width: fit-content;
      margin: 0 auto;
    }

    & .swiper-slide {
      position: relative;
      z-index: 1;
      width: fit-content !important;
    }

    & .swiper-pagination {
      position: relative;
      top: unset;
      bottom: unset;
      left: unset;
      margin-top: 26px;

      @media (min-width: 720px) {
        margin-top: 36px;
      }

      @media (min-width: 1200px) {
        margin-top: 40px;
      }
    }
  }
}

.card-doctor {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border-radius: 10px;
  padding-bottom: 25px;
  overflow: hidden;
  transition: 0.4s ease;
  max-width: 280px;
  text-decoration: none;

  @media (min-width: 720px) {
    width: 340px;
    max-width: 100%;
    padding-bottom: 17px;
  }

  &-image {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    aspect-ratio: 1 / 1.25;
    border-radius: 10px;
    overflow: hidden;

    & > * {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  & p {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: var(--blue);
    margin: 0;
    padding: 25px 20px 0 20px;

    @media (min-width: 720px) {
      font-size: 18px;
      line-height: 22px;
    }

    @media (min-width: 1200px) {
      font-size: 22px;
      line-height: 27.5px;
      padding: 30px 20px 0 20px;
    }
  }

  &-meo {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #585a60;
    padding: 4px 20px 0 20px;

    @media (min-width: 720px) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  &-experience {
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 18px;
    color: var(--blue);
    padding: 18px 20px 0 20px;

    @media (min-width: 720px) {
      font-size: 18px;
      line-height: 22.5px;
    }
  }

  &:hover {
    box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.25);

    & p {
      text-decoration: underline;
      text-decoration-thickness: 2px;
      text-decoration-skip-ink: none;
      text-decoration-skip: 10px;
    }
  }
}

// section doctors part end

