.smile-card {
  position: relative;
  z-index: 1;
  display: block;
  height: 100%;
  width: 90vw;

  @media (min-width: 720px) {
    width: 54vw;
  }

  @media (min-width: 1200px) {
    width: 31vw;
  }

  @media (min-width: 1680px) {
