      border: 0;

      & [slot='second'] {
        display: none;
      }

      &.rendered {
        visibility: inherit;
        width: 100%;
        height: 100%;

        & [slot='first'],
        & [slot='second'] {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        & [slot='second'] {
          display: unset;
        }
      }
    }

    & *:focus-visible {
      outline: none;
    }
  }

  & .smile-name {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: var(--blue);
    margin-top: 3px;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }

    @media (min-width: 720px) {
      font-size: 20px;
      line-height: 25px;
    }
  }

  & .smile-doctor {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #585a60;
    margin-top: 18px;

    @media (min-width: 720px) {
      font-size: 16px;
      line-height: 20px;
      margin-top: 6px;
    }
  }
}

// section smile part end

// section result part start
.section-result {
  position: relative;
  z-index: 1;
  display: block;
  padding: 37px 0;
  overflow: hidden;

  @media (min-width: 720px) {
    padding: 60px 0;
  }

  @media (min-width: 1200px) {
    padding: 70px 0 80px;
  }
}

.result-list {
  display: grid;
  grid-template-columns: repeat(2, 49%);
  grid-template-rows: repeat(2, 1fr);
  row-gap: 8px;
  column-gap: 8px;

  @media (min-width: 720px) {
    row-gap: 20px;
    column-gap: 20px;
  }

  @media (min-width: 992px) {
    grid-template-columns: repeat(3, 1fr);
  }

  @media (min-width: 1200px) {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: auto;
  }
}

.results-image {
  display: none;
  flex: 1;
  width: 30%;
  max-width: 629px;
  border-radius: 14px;
  overflow: hidden;

  @media (min-width: 1200px) {
    display: flex;
    flex-direction: column;
  }

  & > * {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.result-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding: 20px 5px 30px;
  border: 1px solid rgba(44, 53, 109, 0.3);
  border-radius: 10px;
  background: #ffffff;

  @media (min-width: 720px) {
    padding: 38px 12px 30px;
  }

  @media (min-width: 1200px) {
    padding: 45px 10% 30px;
  }

  @media (min-width: 1400px) {
    padding: 45px 15% 30px;
  }

  & svg,
  & img {
    width: fit-content;
    height: 53px;
    object-fit: contain;
    margin-bottom: 13px;

    @media (min-width: 720px) {
      height: 75px;
    }
  }

  & p {
    margin: unset;
    text-align: center;
  }
}

// section result part end

// section consult part start
.section-consult {
  position: relative;
  z-index: 2;
  padding: 40px 0;
  background: linear-gradient(180deg, #1176ea 0%, rgba(17, 118, 234, 0.8) 100%);

  @media (min-width: 720px) {
    padding: 60px 0;
  }

  @media (min-width: 1200px) {
    padding: 50px 0;
    background-image: linear-gradient(195deg, var(--blue) 0%, #1b66bd 100%);
  }

  & .section-title {
    color: #ffffff;
  }

  & .consult-form {
    padding: 0 8.335%;

    @media screen and (max-width: 1199.98px) {
      & > * {
        &:nth-child(1) {
          order: -1;
        }

        &:nth-child(2) {
          order: 1;
        }

        &:nth-child(3) {
          order: 5;
        }

        &:nth-child(4) {
          order: 0;
        }

        &:nth-child(5) {
          order: 4;
        }

        &:nth-child(6) {
          order: 5;
        }

        &:nth-child(7) {
          order: 6;
        }
      }
    }

    @media (min-width: 720px) {
      padding: 0 16.665%;
    }

    @media (min-width: 1200px) {
      padding: 0;
    }
  }
}

.consult-form {
  display: flex;
  flex-direction: column;
  column-gap: 5px;
  row-gap: 5px;
  margin-top: 24px;

  @media (min-width: 720px) {
    column-gap: 12px;
    row-gap: 12px;
    margin-top: 40px;
  }

  @media (min-width: 1200px) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, auto);
    column-gap: 8px;
    row-gap: 8px;

    & > * {
      &:nth-child(1) {
        grid-area: 1 / 1 / 2 / 2;
      }

      &:nth-child(2) {
        grid-area: 1 / 2 / 2 / 3;
      }

      &:nth-child(3) {
        grid-area: 1 / 3 / 3 / 4;
      }

      &:nth-child(4) {
        grid-area: 2 / 1 / 3 / 2;
      }

      &:nth-child(5) {
        grid-area: 2 / 2 / 3 / 3;
      }

      &:nth-child(6) {
        grid-area: 3 / 2 / 4 / 4;
      }
    }
  }

  & .input-textarea {
    resize: none;
  }

  &-agree {
    @media (min-width: 1200px) {
      align-self: center;
    }

    & * {
      color: #ffffff;
    }
  }

  &-link {
    text-decoration: underline;
    text-decoration-skip-ink: none;

    &:hover {
      text-decoration: none;
    }
  }

  & .btn {
    width: fit-content;
    padding: 16px 23%;
    margin: auto;

    @media (min-width: 720px) {
      padding: 20px 20%;
    }

    @media (min-width: 1200px) {
      margin: unset;
      padding: 20px 136px;
    }
  }

  &-submit {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    column-gap: 15px;
    row-gap: 15px;
    margin-top: 15px;

    @media (min-width: 720px) {
      column-gap: 35px;
      row-gap: 35px;
      margin-top: 7px;
    }

    @media (min-width: 1200px) {
      flex-direction: row;
      align-items: center;
      justify-content: flex-end;
      column-gap: 24px;
      row-gap: 24px;
      margin-top: 16px;
    }
  }
}

// section consult part end

// section-map part start
.section-map {
  position: relative;
  z-index: 0;
  width: 100%;
  height: 400px;
  overflow: hidden;

  &.preview {
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      opacity: 1;
      visibility: visible;
      background: rgba(17, 118, 234, 0.125);
      pointer-events: none;
      transition: 0.4s ease;
    }

    &:hover {
      &::before {
        opacity: 0;
        visibility: hidden;
      }
    }
  }

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

// section-map part end
